import Vue from 'vue'
import VueRouter from 'vue-router'

// 导入需要的组件
import Home from '@/views/Home/Home.vue'
import User from '@/views/User/User.vue'

// 把vueRoute安装为vue插件
Vue.use(VueRouter)

// 定义路由规则
const routes = [
  // 路由重定向
  { path: '/', redirect: 'Home' },
  // 定义首页路由规则
  { path: '/home', component: Home },
  // 定义我的路由规则
  { path: '/user', component: User }
]

// 创建路由实例对象
const router = new VueRouter({
  routes
})

export default router
