<template>
  <!-- 根节点加样式命名规范：名字-container -->
  <div class="home-container">

    <!-- 头部区域 fixed属性简写 fixed -->
    <van-nav-bar title="头条" :fixed="true" />

    <!-- 导入注册并使用ArticleInfo组件 -->
    <van-pull-refresh v-model="refreshing" :disabled="finished" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <ArticleInfo v-for="item in artlist" :key="item.id" :title="item.title" :author="item.aut_name"
          :cmtCount="item.comm_count" :time="item.pubdate" :cover="item.cover">
        </ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>

</template>

<script>
// 按需导入api接口
import { getArticleListAPI } from '@/api/articles.js'
import ArticleInfo from '@/components/Article/ArticleInfo.vue'

export default {
  name: 'Home',
  // 注册组件
  components: {
    ArticleInfo
  },
  props: {},
  data () {
    return {
      // 页码值
      page: 1,
      // 每页显示数据数量
      limit: 10,
      // 文章的数组
      artlist: [],
      // 是否正在加载下一页数据,下一页数据请求回来后值改为true
      loading: true,
      // 所有数据是否加载完毕了，没有更多数据了就把值改成true
      finished: false,
      // 是否正在下拉刷新
      refreshing: false
    }
  },
  watch: {},
  computed: {},
  methods: {
    // 封装获取文章列表数据的方法
    async initArticleList (isRefresh) {
      // 发起get请求获取文章列表数据
      const { data: res } = await getArticleListAPI(this.page, this.limit)
      if (isRefresh) {
        // 下拉刷新 新数据在前，旧数据在后
        this.artlist = [...res, ...this.artlist]
        this.isLoading = false
      } else {
        // 上拉加载 旧数据在前，新数据在后
        this.artlist = [...this.artlist, ...res]
        this.loading = false
      }

      if (res.length === 0) {
        // 证明没有下一页数据了，直接把finished改为true表示数据加载完了
        this.finished = true
      }
    },
    // 被调用请求下一页数据
    onLoad () {
      // 让页码值加一
      this.page++
      // 重新请求接口获取数据
      this.initArticleList(true)
    },
    // 下拉刷新的处理函数
    onRefresh () {
      //  让页码值加一
      this.page++
      // 重新请求接口获取数据
      this.initArticleList()
    }
  },
  created () {
    this.initArticleList()
  },
  mounted () { }
}
</script>
<style lang="less" scoped>
.home-container {
  padding: 46px 0 50px 0;
}
</style>
